import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";

// Components
import "./css/Header.css";

export class Header extends Component {
  toggleMode = (searcherMode) => {
    if (this.props.root.mobileScreenWidth >= document.body.clientWidth) {
      // Set searching value as empty string
      this.props.root.actionCreator("CHANGE_SEARCHER_VALUE", "");

      // Switch between toggle searcher modes
      switch (searcherMode) {
        case "COMPACT":
          this.props.root.actionCreator("SWITCH_SEARCHER_MODE", "FULLSIZE");
          return true;

        case "FULLSIZE":
          this.props.root.actionCreator("SWITCH_SEARCHER_MODE", "COMPACT");

          // Delete all markers =\
          this.search({
            search: this.props.searcher.search,
            expression: "",
            items: this.props.searcher.searchItems,
            fast: true // Enable fast searcher
          });

          return true;
      }
    } else {
      this.props.root.actionCreator("SWITCH_SEARCHER_MODE", "DEFAULT");
    };
  };

  setSeacher = (searcherMode) => {
    switch (searcherMode) {
      case "FULLSIZE":
        return "header-searcher searcher_active";
      case "COMPACT":
        return "header-searcher";
      case "DEFAULT":
        return "header-searcher";
    };
  };

  search(props) {
    // Search an expression in some html items
    this.props.root.actionCreator("SEARCH_EXPRESSION", {
      search: props.search,
      expression: props.expression,
      items: props.items,
      fast: false,
    });
  };

  searchAPI = (newValue) => {
    // Set new searching value
    this.props.root.actionCreator("CHANGE_SEARCHER_VALUE", newValue);

    // Search an expression in some html items
    this.search({
      search: this.props.searcher.search,
      expression: this.props.searcher.searcherValue,
      items: this.props.searcher.searchItems,
    });
  };

  render() {
    return (
      <header className={"header"}>
        <div className={"header-container"}>
          <p className={"header-title"}>
            <a href="/">Dooboox</a>
          </p>
          <div className={this.setSeacher(this.props.searcher.searcherMode)}>
            <input
              type="text"
              name="searcher"
              id={"search"}
              value={this.props.searcher.searcherValue}
              onChange={(e) => this.searchAPI(e.target.value)}
            />
            <div
              className={"search-icon_box"}
              onClick={() => {
                this.toggleMode(this.props.searcher.searcherMode);
              }}
            >
              <FontAwesomeIcon icon={faSearch} className={"search-icon"} />
            </div>
          </div>
        </div>
      </header>
    );
  };
};
