import React, { Component } from "react";

export class Text extends Component {
  constructor(props) {
    super();
    this.id = props.id;
    this.value = props.value;
  }

  createText = (props) => {
    if (!props.id) props.id = "";

    return (
      <p className="tp-text" id={props.id}>
        {props.value}
        <br />
      </p>
    );
  };

  render = () => {
    return this.createText({ id: this.id, value: this.value });
  };
}
