import { store } from "../store";

let initalState = {
  compactMenuCurtain: "compact-menu_curtain", // Show/hide cmenu curtain
  mobileScreenWidth: 590, // Mobile screen resolution (px)

  menuMode() {
    // Toggle menu mode
    return document.body.clientWidth <= this.mobileScreenWidth
      ? "COMPACT" // Set compact menu
      : "FULLSIZE"; // Set fullsize menu
  },
};

export const menuReducer = (state = initalState, action) => {
  // Main (at point - root) reducer
  switch (action.type) {
    case "SWITCH_MENU_MODE": {
      // switch compact menu switch mode
      state.compactMenuCurtain = action.payload;
      break;
    }
  }

  return state;
};
