import React, { Component } from "react";
import { NavLink } from "react-router-dom";

export class FullsizeMenu extends Component {
  constructor(props) {
    super();

    this.state = {};
    this.state.location = props.location;
  }

  render() {
    return (
      <div className="menu">
        <menu className="menu-list">
          <li className="menu-item">
            <NavLink to={`/${this.state.location.home}`} className="menu-link">
              ГЛАВНАЯ
            </NavLink>
          </li>

          <li className="menu-item">
            <NavLink
              to={`/${this.state.location.content}`}
              className="menu-link"
            >
              СОДЕРЖАНИЕ
            </NavLink>
          </li>

          <li className="menu-item">
            <NavLink
              to={`/${this.state.location.authors}`}
              className="menu-link"
            >
              АВТОРЫ
            </NavLink>
          </li>

          <li className="menu-item">
            <NavLink to={`/${this.state.location.about}`} className="menu-link">
              О ПРОЕКТЕ
            </NavLink>
          </li>
        </menu>
      </div>
    );
  }
}
