import React, { Component } from "react";
import { BrowserRouter, Route } from "react-router-dom";

// CSS Styles
import "./css/Main.css";

// Components
import { HomePage } from "./pages/HomePage";
import { ContentPage } from "./pages/ContentPage";
import { AuthorsPage } from "./pages/AuthorsPage";
import { AboutPage } from "./pages/AboutPage";
import { Menu } from "./menu/Menu";

export class Main extends Component {
  render() {
    return (
      <BrowserRouter>
          <main>
            <Menu {...this.props} />
            <Route
              exact
              path={`/${this.props.root.location.home}`}
              render={(props) => <HomePage {...this.props} />}
            />
            <Route
              exact
              path={`/${this.props.root.location.content}`}
              component={ContentPage}
            />
            <Route
              exact
              path={`/${this.props.root.location.authors}*`}
              render={(props) => <AuthorsPage {...this.props} />}
            />
            <Route
              exact
              path={`/${this.props.root.location.about}`}
              render={(props) => <AboutPage {...props} />}
            />
          </main>
      </BrowserRouter>
    );
  };
};
