// Expression searching and highlighting

export class Searcher {
  #result; // Predeclare a result ( returning as parse function result )

  constructor(string, elements) {
    this.searchString = this._removeSpecialSymbols(string.toLowerCase().trim());

    // Get any elements ( fu*# flux )
    this.elementsList = elements.map((e) => {
      return document.querySelectorAll(e);
    });
  }

  _fastSearch = () => { // Use if you wanna get result faster (you will have spent 2 - 4 ms)
    this.elementsList.map((els) => {

      els.forEach((e) => {
        if (this.searchString != "") {
          e.innerHTML = this._markRemove(e.innerHTML)
            .replace(new RegExp(this.searchString, 'gi'),
              this._mark(this.searchString));
        } else {
          e.innerHTML = this._markRemove(e.innerHTML);
        }
      });

    });

    // It looks like 2ms - 4ms is spend
  }

  search = (type) => {
    if (!type) {
      // Public API
      this.elementsList.map((els) => {
        // Check all nodes
        els.forEach((e) => {
          // Check all node elements
          if (this.searchString !== "") {
            // Marker found symbols
            e.innerHTML = this._parse(this.searchString, e.innerText);
          } else {
            // Or delete all markers
            e.innerText = e.innerText
              .replace("<mark>", "")
              .replace("</mark>", "");
          }
        });
      });

      // This one is spend 4 - 15 ms to search
    } else this._fastSearch();
    // You can set type = true to use fast search
    // ( fast search replaces any typecases sumbols to lowercase but it's faster )
  };

  _parse = (token, string, result = "") => {
    if (result === "") {
      // Fist iteration

      // Let's find an exp in the given string
      let foundToken = string.match(new RegExp(token, "i"));
      let result = string; // Only here

      if (foundToken !== null) {
        // If we have found something ;)
        let offString = string.substring(foundToken.index + token.length);

        // Update result
        result =
          result.substring(0, foundToken.index) +
          this._mark(foundToken) +
          offString;

        // Recursive call itself ( next condition )
        this._parse(token, offString, result);
      } else {
        // If we haven't found something =<
        return result;
      }
    } else {
      // Here we going find a token again
      let foundToken = string.match(new RegExp(token, "i"));

      // If we have again found some one
      if (foundToken != null) {
        // Get offseted string and result ( main actions here )
        let offString = string.replace(foundToken[0], this._mark(foundToken[0]));
        result = result.replace(string, offString);

        // Redeclare offString to give it itself ( recursive )
        offString = offString.substring(
          foundToken.index + this._mark(foundToken[0]).length
        );

        // Call itself ( Yamete kudasai )
        this._parse(token, offString, result);
      } else {
        this.#result = result;
        // Yeah, we've found it =)
      }
    }

    // Yep-yep
    return this.#result;
  };

  // Can i stay here?
  _mark = (token) => {
    return `<mark>${token}</mark>`;
  };

  _markRemove = (stringHTML) => {
    return stringHTML
      .replace(new RegExp("<mark>", "g"), "")
      .replace(new RegExp("</mark>", "g"), "");
  };

  // If i don't do this one i get a piece of crab
  _removeSpecialSymbols = (string) => {
    return string.replace(/[?/\\!@$%&*+^\p{S}()\[\]]/g, "");
  }
}

